#FAQTitle{
    text-align: center;
    font-weight: 400;
    margin: auto;
    margin-top: 2rem;
    display:block;
}
#FAQTitle::after{
    content:"";
    display: block;
    margin:0 auto;
    width:75%;
    padding-top: 20px;
    border-bottom: 1px solid var(--bubbleGumPink);
}   
.accordion{
    width:85%;
    margin: auto;
    margin-top:2rem;
    border-radius:0.2rem;
    background-color:var(--purple);
}
  .accordionLabel, .accordionContent{
    padding:14px 20px;
}
.accordionLabel{
    display:block;
    font-weight:500;
    cursor:pointer;
    position:relative;
}
.accordionLabel:hover{
    background:rgba(0,0,0,0.1);
}
.accordionLabel::after{
    content:"+";
    position:absolute;
    top:20%;
    right:20px;
    width:12px;
    height:6px;
}
.accordionContent{
    padding:10px;
    background-color:white;
    line-height:1.6;
    background-color: var(--lightPink);
    display: none;
}
.accordionInput{
    display: none;
}
.accordionInput:checked~.accordionContent{
    display:block;
}
.accordionInput:checked~.accordionLabel::after{
    content:"-";
}

#questionNotAnswered{
    width:75%;
    margin: auto;
    margin-top: 2rem;
}
#contactUsLink{
    display: block;
    width:55%;
    margin: auto;
    margin-top: 1rem;
    text-decoration: none;
}
#contactUsLink:hover{
    color:var(--bubbleGumPink);
}
#contactUsLink:active{
    color:var(--purple);
}
