body{
    overflow-y: scroll;
}
.totalCostDetails{
    border:2px solid var(--bubbleGumPink);
}
.totalCostDetails ol li{
    display: flex;  justify-content: space-between;
    margin-bottom: 1rem;
}
hr{
    color:var(--purple)
}
.totalCostDetails ol li span{
    margin-left: 1rem;
}
.generalCustomerInformation, .billingInformation{
    display: flex;
    flex-direction: column;
    width:85%;
    margin: auto;
    gap:0.4rem;
}
.generalCustomerInformation input, .billingInformation input{
    padding:0.4rem 0;
}
.generalCustomerInformation input:hover, .billingInformation input:hover{
    border:0.18rem solid var(--purple);
}
.iconContainer > *{
    margin-left: 1rem;
}
input[type="submit"]{
    display: block;
    margin:auto;
    margin-top:1rem;
    padding:10px 5px;
    width:10rem;
    background-color: var(--purple);
    color:var(--lightPink);
    border: none;
    font-weight: 500;
    font-size: 1rem;
    letter-spacing: 3px;
    margin-bottom: 2rem;
}
input[type="submit"]:hover{
    background-color: var(--lightPink);
    color:var(--purple);
}