.footerContainer{
    background-color: var(--lightPink);
    position:absolute;
    margin-top:1rem;
    width: 100%;
}
.footerContainer .socialMediaIcons{
    display: flex;
    justify-content: center;
    gap:2rem;
    padding:0.8rem 0;
}
.footerContainer ul{
    list-style: none;
    display: flex;
    justify-content: space-around;
}
.footerContainer li  > *{
   text-decoration: none;
   font-size: 1.3rem;
}
.footerContainer li > *:hover, .footerContainer .socialMediaIcons > *:hover{
    border-bottom: 2px solid var(--bubbleGumPink);
}
.footerContainer li>*:active{
    color:var(--bubbleGumPink);
}
.copyRight{
    text-align: center;
    padding:0.4rem 0.4rem 0.8rem 0.4rem;
    height:3rem;
    font-size:1.3rem;
    font-weight: bold;
}
@media (max-width:600px){
    .footerContainer ul{
        flex-direction: column;
        align-items: center;
        gap:0.3rem;
    }
}